nav {
    position: absolute;
    display: inline-block;
    top: 0;
    width: 100vw;
    font-size: 0.7vw;
    animation: slide-in 2s ease-out;
    z-index: 1;
    img{
        position: fixed;
        width: 5vh;
        right:1vw;
        margin-left: 0;
        z-index: 1;
    }
}
nav ul { //Wrap general
    position: relative;
    display: flex;
    flex: 1 1 auto;
    margin: 0;
    padding: 0 30px;
    list-style-type: none;
    li:not(:last-child) {
    margin-right: 0.2vw;
    }
    height: 5vh;
        
    li{ 
        display: block;
        border: 2px solid transparent;
        border-radius: 0 0 15px 15px;
        padding: 10px 12px 35px 12px;
        transition: background 0.2s;
        color: #fafafa;
        text-decoration: none;
        text-transform: uppercase;
        transition: color 0.2s;
        background: #18638f;
        text-align: center;
        width: 10vw;
        &:hover { 
            border-radius: 0;
            background: #5ca4ce;
            ul {
                visibility: visible;
                li ul{ 
                    visibility: hidden;
                }
            }
        }
    }
}
            
nav ul li ul { 
    visibility: hidden;
    position: absolute;
    display: block;
    margin: 7px -14px;
    padding: 0;
        li { 
            color: #ffffff;
            left: -5vw;
            display: block;
            border-radius: 0;
            text-align: left;
            padding: 20px 12px 20px 12px;
            background: #18638f;
            &:hover { 
                background: #5ca4ce;
                    ul {
                    visibility: visible;
                        li ul{ 
                            visibility: hidden;
                        }
                    }
            }
        }
}

nav ul li ul li ul { 
    visibility: hidden;
    position: absolute;
    display: block;
    margin: -24px 140px;
    padding: 0;
                        
    li { 
        color: #ffffff;
        left: -5vw;
        display: block;
        border-radius: 0;
        padding: 10px 12px 10px 12px;
        background: #556277;
        &:hover { 
            background: #5ca4ce;
                ul {
                visibility: visible;
                    li ul{ 
                        visibility: hidden;
                    }
                }
        }
    }
}

#last {
    display: block;
    border-radius: 0 0 15px 15px;               
    width: 10vw;
    overflow:hidden;
}  
  
  
@keyframes slide-in {
    0% {
      top: -50px;
    }
    40% {
      top: 20px;
    }
    70% {
      top: 10px;
    }
    100% {
      top: 15px;
    }
  }